/* src/components/Chatbot/Chatbot.css */
.chatbot-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 60px); /* Adjust height considering header height */
  overflow: hidden; /* Prevents container from expanding */
}

.chat-container {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  width: 400px;
  max-width: 100%;
  padding: 20px;
  overflow-y: auto; /* Allows scrolling within the chat container */
  max-height: calc(100vh - 120px); /* Adjust height based on header and bottom spacing */
}

.message {
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 4px;
  clear: both;
  max-width: 80%;
}

.message.me {
  background-color: #e1ffc7;
  align-self: flex-end;
  text-align: right;
  float: right;
}

.message.bot {
  background-color: #f1f1f1;
  align-self: flex-start;
  float: left;
}

.input-container {
  display: flex;
}

.input-container input {
  flex-grow: 1;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-right: 10px;
}

.input-container button {
  padding: 6px 12px; /* Adjusted padding for smaller button */
  background-color: #ff2200;
  border: none;
  border-radius: 4px;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.3s;
}

.input-container button:hover {
  background-color: #0056b3;
}
