body, html {
  margin: 0;
  padding: 0;
  height: 100%; /* Ensure the HTML and body take up full viewport height */
  overflow: hidden; /* Prevent scrolling on the whole page */
}

.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;  /* Ensures the app fills the entire viewport height */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start; /* Aligns content to start below the header */
  font-size: calc(10px + 2vmin);
  color: white;
  padding-top: 60px; /* Padding to accommodate fixed header height */
  box-sizing: border-box; /* Includes padding in height calculation */
  overflow: hidden; /* Prevents scrolling within the app */
}

.App-content {
  margin-top: 60px; /* Match this with your navbar height */
  width: 100%;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: auto;
}


.App-header {
  background-color: #20232a;
  height: 50px; /* Adjust based on your header's actual height */
  padding: 10px 20px;
  font-size: 18px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 1000; /* Ensures header stays on top */
}

.App-title {
  font-weight: bold;
  font-size: 24px;
  color: white;
}

.nav-links a {
  text-decoration: none; /* Remove underline */
}

.nav-button {
  background-color: #007bff; /* Blue color for initial state */
  border: none;
  color: #fff;
  padding: 10px 15px;
  margin: 0 10px 0 0; /* Right margin adjusted for all buttons */
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s, transform 0.2s;
  min-width: 120px; /* Ensure both buttons are the same size */
  text-align: center; /* Center button text */
}

.nav-button:hover {
  background-color: #6c757d; /* Grey color on hover */
  transform: scale(1.05);
}

.nav-button:last-child {
  margin-right: 20px; /* Additional margin for the last button */
}



